import { apiInstance } from "./index.js";

const api = apiInstance();

function listArticle(param, success, fail) {
  api
    .get(`/qboard/`, { params: param }, console.log(""))
    .then(success)
    .catch(fail);
}

function writeArticle(article, success, fail) {
  api.post(`/qboard`, JSON.stringify(article)).then(success).catch(fail);
}

function getArticle(articleno, success, fail) {
  api.get(`/qboard/${articleno}`).then(success).catch(fail);
}

function modifyArticle(article, success, fail) {
  api
    .put(`/qboard/${article.articleno}`, JSON.stringify(article))
    .then(success)
    .catch(fail);
}

function deleteArticle(articleno, success, fail) {
  api.delete(`/qboard/${articleno}`).then(success).catch(fail);
}

function getAnswer(articleno, success, fail) {
  api.get(`/aboard/${articleno}`).then(success).catch(fail);
}

function getAnswerDetail(articleno, success, fail) {
  api.get(`/aboard/answer/${articleno}`).then(success).catch(fail);
}

function writeAnswer(article, success, fail) {
  api.post(`/aboard`, JSON.stringify(article)).then(success).catch(fail);
}

function modifyAnswer(article, success, fail) {
  api
    .put(`/aboard/${article.articleno}`, JSON.stringify(article))
    .then(success)
    .catch(fail);
}

function deleteAnswer(articleno, success, fail) {
  api.delete(`/aboard/${articleno}`).then(success).catch(fail);
}

export {
  listArticle,
  writeArticle,
  getArticle,
  modifyArticle,
  deleteArticle,
  getAnswer,
  writeAnswer,
  modifyAnswer,
  deleteAnswer,
  getAnswerDetail,
};
