<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <b-alert show><h3>답글 목록</h3></b-alert>
      </b-col>
    </b-row>
    <b-row class="mb-1 row justify-content-end">
      <b-col md="1.5">
        <b-form-select v-model="perPage" :options="options"></b-form-select>
      </b-col>
      <b-col md="1.5" class="text-right">
        <b-button variant="outline-primary" @click="moveWrite()"
          >답변쓰기</b-button
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col v-if="articles.length">
        <b-table
          id="board-table"
          :items="articles"
          :fields="fields"
          :per-page="perPage"
          :current-page="currentPage"
          hover
          responsive
          striped
          head-variant="dark"
        >
          <template #cell(regtime)="data">
            {{ data.value }}
          </template>
          <template #cell(actions)="row">
            <b-button
              variant="outline-info"
              size="sm"
              @click="moveModify(row.item)"
              class="mt-1"
              v-if="checkId(row.item.userid)"
            >
              수정
            </b-button>

            <b-button
              variant="outline-danger"
              size="sm"
              @click="moveDelete(row.item)"
              class="mt-1"
              v-if="checkId(row.item.userid)"
            >
              삭제
            </b-button>
          </template>
        </b-table>
        <b-pagination
          v-model="currentPage"
          :total-rows="articles.length"
          :per-page="perPage"
          aria-controls="board-table"
          first-number
          last-number
        ></b-pagination>
      </b-col>
      <b-col v-else class="text-center">답글이 없습니다.</b-col>
    </b-row>
  </b-container>
</template>

<script>
import { getAnswer } from "@/api/qna";
import moment from "moment";
import { mapState } from "vuex";
const memberStore = "memberStore";
export default {
  name: "AnswerList",
  props: {
    questionno: Number,
  },
  data() {
    return {
      articles: [],
      fields: [
        {
          key: "content",
          label: "답글",
          tdClass: "tdSubject",
          sortable: true,
        },
        { key: "userid", label: "작성자", tdClass: "tdClass", sortable: true },
        { key: "regtime", label: "작성일", tdClass: "tdClass", sortable: true },
        { key: "actions", label: "수정&삭제", tdClass: "tdClass" },
      ],
      perPage: 5,
      currentPage: 1,
      options: [
        { value: 5, text: "5개" },
        { value: 10, text: "10개" },
        { value: 20, text: "20개" },
      ],
      nowUser: "",
    };
  },
  computed: {
    ...mapState(memberStore, ["userInfo"]),
  },
  created() {
    getAnswer(this.$route.params.articleno, (response) => {
      this.articles = response.data;
      console.log(this.articles);
    });

    this.nowUser = this.userInfo.userid;
  },
  methods: {
    moveWrite() {
      //alert(this.$route.params.articleno);
      this.$router.push({
        name: "answerRegister",
        params: { questionno: this.questionno },
      });
    },
    moveModify(article) {
      if (this.checkId(article.userid)) {
        this.$router.push({
          name: "answerModify",
          params: {
            questionno: article.questionno,
            answerno: article.answerno,
          },
        });
      } else {
        alert("수정 권한이 없습니다!");
      }
    },
    moveDelete(article) {
      if (this.checkId(article.userid)) {
        this.$router.push({
          name: "answerDelete",
          params: {
            questionno: article.questionno,
            answerno: article.answerno,
          },
        });
      } else {
        alert("삭제 권한이 없습니다!");
      }
    },
    checkId(userid) {
      return this.nowUser === userid ? true : false;
    },
  },
  filters: {
    dateFormat(regtime) {
      return moment(new Date(regtime)).format("YY.MM.DD");
    },
  },
};
</script>

<style scope>
.tdClass {
  width: 50px;
  text-align: center;
}
.tdSubject {
  width: 300px;
  text-align: left;
}
</style>
